.creator__chapter {
  flex-direction: column;
  align-items: start;
  margin-bottom: 30px;
  display: flex;
}

.creator__title {
  margin: 0 0 15px;
  font-size: 24px;
  font-weight: 900;
}

.creator__descr {
  margin: 0;
}

.creator__form {
  flex-direction: column;
  width: 100%;
  display: flex;
}

.creator__label {
  width: 100%;
}

.creator__label:not(:last-child) {
  margin-bottom: 30px;
}

.creator__input {
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 16px;
  width: 100%;
  height: 32px;
  padding: 0 15px;
}

.creator__input--text {
  resize: none;
  min-height: 220px;
  padding-top: 15px;
  padding-bottom: 15px;
}

.readonly {
  color: red;
}

@media (width <= 768px) {
  .creator {
    flex-direction: column;
    align-items: center;
    display: flex;
  }
}

.images__block {
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.images__div {
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;
  border: 1px solid gray;
  width: 100%;
  min-height: 80px;
  margin-bottom: 20px;
  padding: 5px;
  position: relative;
}

.images__div img {
  border: 1px solid #000;
  width: 100px;
  height: 80px;
}

.label__clean {
  z-index: 3;
  position: relative;
}

.tags__label {
  width: 100%;
  position: relative;
}

.select-btn {
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 16px;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  margin-bottom: 8px;
  padding: 6px 15px;
  display: flex;
}

.select-btn .btn-text {
  color: #333;
  font-size: 17px;
  font-weight: 400;
}

.arrow-down {
  color: #fff;
  border: 2px solid var(--mainBtns-color);
  box-shadow: 0px 1px 0px 0px var(--mainBtns-color);
  background: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 14px;
  transition: all .3s;
  display: flex;
}

.select-btn.open .arrow-dwn {
  transform: rotate(-180deg);
}

.tags__box {
  z-index: 6;
  border: 2px solid var(--mainBtns-color);
  background-color: #fff;
  border-radius: 16px;
  width: 100%;
  max-width: 220px;
  max-height: 200px;
  margin-top: 15px;
  padding: 4px;
  display: none;
  position: absolute;
  overflow-y: auto;
}

.tags__items {
  padding: 0;
}

.select-btn.open ~ .tags__box {
  display: block;
}

.tags__items .tag {
  cursor: pointer;
  border-radius: 16px;
  align-items: center;
  height: 50px;
  padding: 0 15px;
  list-style: none;
  transition: all .3s;
  display: flex;
}

.tag:not(:last-child) {
  margin-bottom: 5px;
}

.tag.checked {
  background-color: var(--activeMainBtns);
  border: 2px solid var(--mainBtns-color);
  display: none;
}

.tag.hidden {
  display: none;
}

.tags__checked {
  min-height: 42px;
  display: flex;
}

.arrow-down svg {
  transition: transform .3s ease-in-out;
}

.select-btn.open .arrow-down svg {
  transform: rotate(180deg);
}

.tags__checked {
  justify-content: center;
  gap: 15px;
  margin-bottom: 15px;
  padding: 6px;
  display: flex;
}

.checked-tag {
  box-shadow: 0px 1px 0px 0px var(--mainBtns-color);
  color: var(--mainBtns-color);
  border: 2px solid var(--mainBtns-color);
  background-color: #ffbd12;
  border-radius: 32px;
  align-items: center;
  gap: 5px;
  padding: 2px 4px;
  font-size: 16px;
  font-weight: 500;
  display: flex;
}

.checked-tag__remove {
  cursor: pointer;
  transition: all .3s ease-in-out;
}

@media (hover: hover) {
  .checked-tag__remove:hover, .select-btn:hover .arrow-down {
    transform: scale(.8);
  }

  .tag:hover {
    background-color: var(--activeMainBtns);
  }
}

.tags__search {
  background-color: var(--activeMainBtns);
  border: none;
  border-radius: 16px;
  width: 100%;
  padding: 6px 10px 6px 6px;
}

.tags__search::placeholder {
  font-size: 14px;
}
